<template >
    <div class="fund">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item v-for="banner,i in banner" :keys="i">
                <van-image :src="banner.icon_url"></van-image>
            </van-swipe-item>
        </van-swipe>
        <div class="tab">
            <van-tabs type="card" >
                <van-tab v-for="(tab,index) in tabs" :key="index" :title="tab.tab_name">
                    <div class="tab-desc">
                        <h4>{{tab.comment}}</h4>
                        <p>{{tab.tips}}</p>
                    </div>
                    <van-cell-group inset>
                        <van-cell v-for="item,cellIndex in tab.items" :key="cellIndex">
                            <div class="fund-item">
                                <van-image width="100" height="100" :src="item.img_src" :lazy-load="true"></van-image>
                                <span class="fund-name">{{item.name}}</span>
                                <span class="fund-pe">
                                    <span class="value">{{item.content[0].value}}</span>
                                    <span class="desc">{{item.content[0].desc}}</span>
                                </span>
                                <span class="fund-roe">
                                    <span class="value">{{item.content[1].value}}</span>
                                    <span class="desc">{{item.content[1].desc}}</span>
                                </span>
                            </div>
                        </van-cell> 
                    </van-cell-group>
                </van-tab>
                
            </van-tabs>
        </div>
        
    </div>
</template>

<script >
import * as api from "@/api/index"
export default{
    data(){
        return{
            banner:[],
            tabs:[]
        }
    },
    async mounted(){
        // let result = await api.getFundBanner()
        // this.banner= result.data.items
        // console.log(result)
        try{
            api.getFundBanner().then((result) => {
                this.banner= result.data.items
            })
        }catch(error){
            console.log(error)
        }
        
        // .catch((error)=>{
        //     console.log(error)
        // })
        // let resultTab = await api.getFundTablist()
        // console.log(resultTab)

        (async () => {
            let resultTab = await api.getFundTablist()
            // console.log(resultTab)
            this.tabs = resultTab.data.tab
        })()
    }
}
</script>

<style scoped lang="less">
.fund{
    ::v-deep .van-swipe__indicator{
        background: #666;
    }
    .tab-desc{
        text-align: left;
        margin: 0.8rem;
        h4{
            margin: 0.3rem;
            color: orange;
        }
        p{
            margin: 0.3rem;
            color: #999;
        }
    }
    .fund-item{
        display: flex;
        align-items: center;
        justify-content: space-around;
        .fund-pe,.fund-roe{
            margin: 0.1rem;
        }
    }
    .fund-item>span{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    .van-cell{
        padding: 0;
    }

}
</style>